@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;800&family=Montserrat:wght@300;500;800;900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', sans-serif;
}

header {
  width: 90vw;
  margin: 0 auto;
}
nav,
header > a {
  display: none;
}
main {
  width: 90vw;
  margin: 0 auto;
}
div.hero-text > div {
  display: inline-block;
  padding: 0.5rem 1.25rem;
  background-color: #F7FFE8;
  border-radius: 1.25rem;
  text-transform: uppercase;
  color: #79B300;
  font-weight: 700;
  margin-bottom: 0.5rem;
}
div.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  width: 90vw;
  margin: 0 auto 5rem;
}
h1 {
  margin: 2rem 0 1rem;
  line-height: 1.2;
  font-family: 'Monteserrat', sans-serif;
}
h1 > span {
  color: #79B300;
}
div.hero-text > p {
  font-weight: 200;
  line-height: 1.5;
  margin-bottom: 1rem;
}
div.hero-text > a {
  display: block;
  width: 12rem;
  text-align: center;
  padding: 1rem;
  background-color: #79B300;
  border-radius: 1.5rem;
  text-decoration: none;
  color: #fff;
  font-family: 'Monteserrat', sans-serif;
}
div.hero-img > img {
  width: 100%;
}
h3.features-header {
  font-family: 'Montserrat', sans-serif;
  color: #79B300;
  font-weight: 900;
  text-align: center;
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}
div.features > p {
  line-height: 1.5;
  text-align: center;
}
div.featureList {
  margin: 5rem 0;
}
div.send {
  background-color: #F7FFE8;
  border-radius: 1.5rem;
  padding: 2rem 1.5rem 0;
  margin-bottom: 1.5rem;
}
div.sendText > img {
  margin-bottom: 1rem;
}
div.sendText > p {
  line-height: 1.5;
  margin-bottom: 1rem;
}
div.sendText > a {
  display: block;
  width:9rem;
  text-align: center;
  padding: 1rem;
  background-color: #79B300;
  border-radius: 1.5rem;
  font-family: 'Monteserrat', sans-serif;
  text-decoration: none;
  color: #fff;
  margin-bottom: 2rem;
}
div.sendImage {
  display: flex;
}
div.sendImage > img {
  width: 100%;
}
div.lock {
  background-color: #f5f5f5;
  border-radius: 1.5rem;
  padding-bottom: 2rem;
}
div.lock > img {
  width: 100%;
}
div.lock > p {
  font-size: 1.5rem;
  margin-top: 3rem;
  margin-left: 2rem;
  width: 14rem;
  line-height: 1.5;
}

div.lock > p > span {
  color: #000;
  font-weight: 800;
}

div.contribution {
  background-color: #f5f5f5;
  border-radius: 1.5rem;
  padding-top: 3rem;
  margin-top: 1.5rem;
}

div.contribution > p {
  font-size: 1.5rem;
  margin-top: 3rem;
  margin-left: 2rem;
  width: 12rem;
  line-height: 1.5;
}

div.contribution > p > span {
  color: #000;
  font-weight: 800;
}

div.contribution > img {
  width: 100%;
}

div.aboutUs {
  margin-bottom: 6rem;
}

div.aboutUs > p.subheader {
  text-align: center;
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

div.aboutUs > p.text {
  line-height: 1.5;
  text-align: center;
  margin: 0 auto 4rem;
}

div.moreInfo {
  display: flex;
  flex-direction: column;
  color: #fff;
  background-color: #79B300;
  border-radius: 1.5rem;
  padding: 2rem 1rem 0;
}

div.infoText {
  display: flex;
  flex-direction: column;
}

div.moreInfo > img {
  width: 100%;
  margin-top: 1.4rem;
}

div.section {
  margin-bottom: 3rem;
}

div.section > h3 {
  color: #fff;
  margin-bottom: 1rem;
  font-size: 1.5rem;
}

div.aboutUs + h2 {
  text-align: center;
  font-size: 2rem;
  margin: 0 auto 3rem;
}

div.aboutUs + h2 + a {
  display: block;
  width: 12rem;
  text-align: center;
  padding: 1rem;
  background-color: #79B300;
  border-radius: 1.5rem;
  font-family: 'Monteserrat', sans-serif;
  text-decoration: none;
  color: #fff;
  margin: 0 auto 5rem;
}

footer {
  background-color: #79B300;
  padding: 3rem 1.3rem;
  color: #fff;
  text-align: center;
}

div.footer-top > h2 {
  margin-bottom: 2rem;
}

div.footer-top > p {
  margin: 0 auto 2.5rem;
}

div.footer-bottom {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  gap: 2rem;
  margin-top: 2.5rem;
}

div.footer-bottom > a {
  color: #fff;
  transition: transform 0.5s ease;
}

div.social-icons {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
}

@media screen and (min-width: 1000px) {
  header {
    width: 90vw;
    height: 5rem;
    margin: 3rem auto 5rem;
    display: grid;
    grid-template-columns: 15vw 32vw 23vw;
    gap: 10vw;
    align-items: center;
    color: #000;
  }
  nav,
  header > a {
    display: inline;
  }
  header > img {
    width: 3rem;
    transition: transform 0.5s ease;
  }
  header > img:hover {
    transform: scale(1.2);
  }
  nav {
    display: flex;
    justify-content: space-evenly;
    font-size: 1rem;
  }
  a {
    font-family: 'Montserrat', sans-serif;
    text-decoration: none;
    color: #000;
    transition: transform 0.5s ease;
  }
  header > a {
    width: 56%;
    margin-left: auto;
    text-align: center;
    padding: 1rem;
    background-color: #79B300;
    border-radius: 1.5rem;
    font-size: 1rem;
    color: #fff;
    transition: transform 0.5s ease;
  }
  div.hero {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 90vw;
    margin: 0 auto 5rem;
  }
  h1 {
    margin: 2rem 0 1rem;
    font-size: 3.5rem;
    line-height: 1.2;
    font-family: 'Inter', sans-serif;
  }
  span {
    color: #79B300;
  }
  div.hero-text > p {
    font-weight: 200;
    width: 26rem;
    line-height: 1.5;
    font-family: 'Inter', sans-serif;
    margin-bottom: 3rem;
  }
  div.hero-text > a {
    display: block;
    width: 12rem;
    text-align: center;
    padding: 1rem;
    background-color: #79B300;
    border-radius: 1.5rem;
    font-size: 1rem;
    color: #fff;
  }
  div.features {
    margin-bottom: 6rem;
  }
  h3.features-header {
    font-family: 'Montserrat', sans-serif;
    color: #79B300;
    font-weight: 900;
    text-align: center;
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
  }
  div.features > p {
    line-height: 1.5;
    text-align: center;
    width: 55vw;
    margin: 0 auto;
    font-family: 'Inter', sans-serif;
  }
  
  div.featureList {
    width: 90vw;
    margin: 0 auto;
    display: grid;
    align-items: center;
    grid-template-columns: 42vw 23vw 22vw;
    gap: 1rem;
    margin-top: 5rem;
    font-family: 'Inter', sans-serif;
  }
  
  div.send {
    background-color: #f5f5f5;
    border-radius: 1.5rem;
    display: flex;
    height:33rem;
    padding: 2rem 1.5rem 0;
    margin-bottom: 0;
    transition: transform 0.5s ease;
  }
  
  div.sendText {
    display: grid;
    align-content: center;
  }
  
  div.sendText > img {
    margin-bottom: 2rem;
  }
  
  div.sendText > p {
    font-size: 2rem;
    line-height: 1.5;
    width: 17rem;
    margin-bottom: 3rem;
  }   
  
  div.sendText > a {
    width:9rem;
    text-align: center;
    padding: 1rem;
    background-color: #79B300;
    border-radius: 1.5rem;
    font-size: 1rem;
    color: #fff;
    margin-bottom: 2rem;
  }
  
  div.sendImage {
    display: flex;
    align-items: flex-end;
  }
  
  div.lock {
    background-color: #f5f5f5;
    border-radius: 1.5rem;
    transition: transform 0.5s ease;
  }
  
  div.lock > p {
    font-size: 1.5rem;
    margin-top: 3rem;
    margin-left: 2rem;
    line-height: 1.5;
    font-family: 'Inter', sans-serif;
  }
  
  div.lock > p > span {
    font-family: 'Inter', sans-serif;
    color: #000;
    font-weight: 800;
  }
  
  div.contribution {
    padding-top: 0;
    margin-top: 0;
    transition: transform 0.5s ease;
  }
  
  div.contribution > p {
    font-size: 1.5rem;
    margin-top: 3rem;
    margin-left: 2rem;
    width: 11rem;
    line-height: 1.5;
    font-family: 'Inter', sans-serif;
  }
  
  div.contribution > p > span {
    font-family: 'Inter', sans-serif;
    color: #000;
    font-weight: 800;
  }
  
  div.contribution > img {
    width: 100%;
  }
  
  div.send:hover,
  div.lock:hover,
  div.contribution:hover {
    background-color: #F7FFE8;
    transform: scale(1.1);
  }
  
  div.aboutUs {
    margin-bottom: 6rem;
  }
  
  div.aboutUs > p.subheader {
    text-align: center;
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
    font-family: 'Inter', sans-serif;
  }
  
  div.aboutUs > p.text {
    line-height: 1.5;
    font-family: 'Inter', sans-serif;
    text-align: center;
    width: 48rem;
    margin: 0 auto 4rem;
  }
  
  div.moreInfo {
    color: #fff;
    background-color: #79B300;
    width: 90vw;
    margin: 0 auto;
    border-radius: 1.5rem;
    display: flex;
    flex-direction: row;
    gap: 5rem;
    padding: 4.5rem 4rem 0;
  }
  
  div.moreInfo > img {
    margin-top: 0;
  }
  
  div.section {
    margin-bottom: 5rem;
  }
  
  div.section > h3 {
    color: #fff;
    margin-bottom: 1rem;
    font-size: 1.5rem;
  }
  
  div.aboutUs + h2 {
    font-size: 3rem;
    width: 30rem;
  }
  
  
  
  footer {
    background-color: #79B300;
    padding: 6vw;
    color: #fff;
    text-align: center;
  }
  
  div.footer-top > h2 {
    margin-bottom: 1rem;
  }
  
  div.footer-top > p {
    margin: 0 auto 2.5rem;
    width: 40rem;
  }
  
  div.footer-bottom {
    display: grid;
    grid-template-columns: 21rem 10rem 9rem;
    align-items: center;
    gap: 17rem;
    margin-top: 2.5rem;
  }
  
  div.footer-bottom > a {
    color: #fff;
    transition: transform 0.5s ease;
  }
  
  div.footer-bottom > a:hover {
    text-decoration: underline;
    transform: scale(1.2);
  }
  
  div.social-icons {
    display: flex;
    justify-content: flex-end;
    gap: 1rem;
  }
  
  a:hover {
    transform: scale(1.2);
  }
}